/* eslint-disable no-restricted-globals */
import React, { Component } from 'react';
import './App.scss';
import HeadToHead from './components/HeadToHead/HeadToHead';
import MatchCard from './components/MatchCard/MatchCard';
import SideCard from './components/SideCard/SideCard';
import Twitch from './components/Twitch/Twitch';
import { Players, ReidDuke, MartinJuza, getPlayer } from './assets/playerDefaults';
import Bracket from './components/Bracket/Bracket';
import Tabs from './components/Tabs/Tabs';

class App extends Component {
  state = {
    activeMatch: { playerA: ReidDuke, playerB: MartinJuza},
    bracketHover: { playerA: 'Luis Scott-Vargas', playerB: 'Reid Duke'},
    group: 'A'
  }
  constructor(props) {
    super(props)
    // eslint-disable no-restricted-globals
    const HOST = location.origin.includes('localhost') ? 'ws://localhost:3001' : 'https://mtg-viewer.herokuapp.com:3002'
    this.ws = new WebSocket(HOST)
    this.ws.onmessage = (event) => {
      const message = JSON.parse(event.data)
      console.log(message)
      const data = message.data
      if (message.type === 'liveMatch') {
        const playerA = getPlayer(data.playerA)
        const playerB = getPlayer(data.playerB)
        this.setState({
          activeMatch: {
            playerA,
            playerB,
            game1: data.game1,
            game2: data.game2,
            game3: data.game3,
          }
        })
      }
      if (message.type === 'brackets') {
        this.setState({
          brackets: data,
        })
      }
      console.log(this.state)
    }
  }
  render() {
    const { activeMatch, bracketHover, brackets, group } = this.state
    const bracket = brackets ? brackets.find(item => item.group === group).bracket : undefined
    return (
      <div className="App">
        <div className='live-match'>
          <SideCard side='left' player={activeMatch.playerA} game1={activeMatch.game1} game2={activeMatch.game2} game3={activeMatch.game3} />
          <Twitch url='magic' />
          <SideCard side='right' player={activeMatch.playerB} game1={activeMatch.game1} game2={activeMatch.game2} game3={activeMatch.game3} />
        </div>
        <HeadToHead rightWins={3} leftWins={6}></HeadToHead>
        <div className='bracket-viewer'>
          <Tabs />
          <div className="bracket-container">
            <Bracket bracket={bracket} onActiveChange={(bracketHover) => this.setState({ bracketHover })} />
            <MatchCard playerA={bracketHover.playerA} playerB={bracketHover.playerB} winsPlayerA={3} winsPlayerB={5}></MatchCard>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
