import React from 'react';
import './styles.scss'
import { triangleShape } from '../../assets/shapes';
import Deck from '../Deck/Deck';

const SideCard = ({ side = 'left', player, game1, game2, game3 }) => {
  const expectedResult = side === 'left' ? 'a' : 'b'
  return (
    <div className={`side-card ${side}`} >
      <div className='player'>
        <div className='background'>
          {triangleShape()}
        </div>
        <div className='image'> 
          <img src={player.image}/>
          <div className='name'>
            {player.name}
          </div>
        </div>
        <div className='decks'>
          <Deck deck={game1 ? player[game1.deckA] : 'TBD'} status={game1 && game1.result === expectedResult ? 'win' : 'loss'} />
          <Deck deck={game2 ? player[game2.deckA] : 'TBD'} status={game2 && game2.result === expectedResult ? 'win' : 'loss'} />
          {game3 ? (
            <Deck deck={game3 ? player[game3.deckA] : 'TBD'} status={game3.result === expectedResult ? 'win' : 'loss'} />
          ) : (
            <Deck deck='TBD' /> 
          )}
        </div>
      </div>
    </div>
  );
}

export default SideCard;